body {
  margin: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header--padding {
  margin-bottom: 158px;
}

.header {
  background-color: #ffb8ca;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.header__button {
  width: 159px;
  height: 44px;
  color: #d41443;
  background: #fff;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  transition-duration: .4s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: none;
}

.header__button:hover, .header__button:active {
  background-color: #ecebeb;
}

.header__sections {
  display: none;
}

.header__navigation {
  align-items: center;
  gap: 61px;
  display: flex;
}

.hero__title {
  z-index: 1;
  margin-top: 50px;
  margin-bottom: 24px;
  position: relative;
}

.hero__title h1 {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #d41443;
  margin: 0;
  font-family: Titan One;
  font-size: 45px;
  font-style: normal;
  font-weight: 400;
  line-height: 44px;
}

.hero__title h2 {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: #fff;
  margin: 0;
  font-family: Titan One;
  font-size: 38px;
  font-style: normal;
  font-weight: 400;
  line-height: 44px;
}

.hero__buttons {
  z-index: 1;
  gap: 12px;
  display: flex;
  position: relative;
}

.hero--button-style {
  text-align: center;
  color: #d41443;
  border: none;
  border-radius: 22px;
  align-items: center;
  padding: 11px 15px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  transition-duration: .4s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.hero--button-style:hover, .hero--button-style:active {
  color: #ecebeb;
  background-color: #d41443;
}

.hero__pink-ellipse {
  width: 400px;
  height: 400px;
  z-index: 0;
  position: absolute;
  top: 100px;
  left: 50%;
  overflow: hidden;
}

.hero__icecream-pic {
  position: absolute;
  bottom: -3px;
  left: 60%;
}

.hero__origin-pic {
  height: 400px;
  position: absolute;
  bottom: 3px;
}

.facts {
  display: none;
}

.hamburger {
  width: 30px;
  height: 30px;
  color: #d41443;
  align-items: center;
  display: flex;
}

.button {
  background: none;
  border: none;
}

.mobile-menu {
  display: none;
  position: relative;
}

.mobile-menu.is-open {
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: #e17992;
  justify-content: center;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.mobile-menu.is-open .mobile-menu__button {
  text-align: center;
  color: #d41443;
  border: none;
  border-radius: 22px;
  align-items: center;
  margin-bottom: 32px;
  padding: 20px 70px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  display: flex;
}

.mobile-menu.is-open .mobile-menu__button:hover, .mobile-menu.is-open .mobile-menu__button:active {
  color: #fff;
  background-color: #d41443;
  transition-duration: .4s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.mobile-menu.is-open .mobile-menu__pages {
  flex-direction: column;
  gap: 23px;
  list-style-type: none;
  display: flex;
  position: absolute;
  left: 20px;
}

.mobile-menu.is-open .mobile-menu__pages .mobile-menu__link {
  color: #fff;
  align-items: center;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  display: flex;
}

.mobile-menu.is-open .mobile-menu__buyme {
  margin-right: 10px;
}

.button--close-mobile-menu {
  width: 35px;
  height: 35px;
  color: #fff;
  background: none;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu_container {
  margin-top: 60px;
  margin-left: 21px;
  margin-right: 21px;
}

.hero__advertisement-with-pic {
  display: none;
}

