*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  margin: 0 auto;
  font-family: DM Sans, sans-serif;
}

h1, h2, h3, h4, h5 {
  text-transform: uppercase;
  font-family: Titan One, cursive;
  font-weight: 400;
}

image {
  display: block;
}

ul {
  list-style: none;
}

.container--how-its-made {
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767.98px) {
  .container--how-its-made {
    width: 320px;
    padding: 60px 20px 66px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199.98px) {
  .container--how-its-made {
    width: 768px;
    padding: 60px 34px 205px 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container--how-its-made {
    width: 1200px;
    padding: 60px 96px 179px 16px;
  }
}

.link {
  color: inherit;
  text-decoration: none;
  list-style: none;
}

.list {
  list-style: none;
  display: flex;
}

.container {
  width: 100%;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    padding: 0 16px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    margin: 0 auto;
  }
}

.section__subheading, .section__heading {
  letter-spacing: .04em;
  text-align: center;
  text-transform: capitalize;
  font-family: Titan One;
  line-height: 1.145;
}

.section__subheading {
  color: #ffa5ba;
  font-size: 18px;
}

.section__heading {
  color: #d41443;
  letter-spacing: 4%;
  margin-top: 18px;
  font-size: 34px;
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
  .section--products {
    padding-left: 16px;
    padding-right: 16px;
  }

  .section__subheading {
    font-size: 26px;
  }

  .section__heading {
    letter-spacing: 4%;
    font-size: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .section__subheading {
    letter-spacing: 4%;
    font-size: 34px;
  }

  .section__heading {
    letter-spacing: 6%;
    font-size: 58px;
  }
}

.section--products {
  background-color: #fff;
  padding: 60px 0;
}

.products {
  width: 100%;
  grid-row: 1 / 3;
  row-gap: 140px;
  margin-top: 160px;
  display: grid;
}

.products__item {
  color: #fff;
  letter-spacing: .04em;
  text-align: center;
  width: 100%;
  border-radius: 24px;
  padding: 160px 32px 40px;
  position: relative;
}

.products__item:hover {
  cursor: pointer;
  box-shadow: 0 5px 5px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.products__item--icecream {
  background-color: #ffb8ca;
}

.products__item--icecoffee {
  background-color: #f0d1a5;
}

.products__item--milkshakes {
  background-color: #c2e297;
}

.products__title {
  letter-spacing: .04em;
  text-align: center;
  text-transform: capitalize;
  font-family: Titan One;
  font-size: 20px;
  line-height: 1.145;
  position: relative;
}

.products__title:after {
  content: "";
  width: 24.84px;
  border-bottom: 6px dotted #fff;
  position: absolute;
  top: 69px;
  left: calc(50% - 12.42px);
}

.products__text {
  margin-top: 64px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.76;
}

@media screen and (min-width: 768px) {
  .products {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
  .products {
    column-gap: 16px;
    margin-top: 159px;
  }

  .products__item {
    padding: 118px 18px 40px;
  }

  .products__title {
    font-size: 20px;
  }

  .products__text {
    font-size: 14px;
  }
}

@media screen and (min-width: 1200px) {
